@use "../foundation" as *;

.c-title {
    color: $color-main;
    opacity: .3;
    @include rem(50);
    font-weight: bold;
    font-style: italic;
    @include mq(lg){
        @include rem(80);
    }
}

.c-title-ja {
    color: $color-dark;
    font-weight: bold;
    position: relative;
    z-index: 2;
    margin-left: 1em;
    @include rem(18);
    letter-spacing: .1em;
    margin-top: -7%;
    @include mq(lg){
        margin-top: -5%;
        letter-spacing: .3em;
        @include rem(30);
        margin-left: 2em;
    }
}
