@use "../foundation" as *;

.l-header {
    height: 60px;
    background-color: $color-white;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 100;
    @include mq(lg){
        height: 100px;
    }
}

.l-header_inner {
    display: flex;
    height: 60px;
    align-items: center;
    @include mq(lg){
        height: 100px;
    }
}

.l-header_nav {
    @include rem(16);
    width: 100%;
    text-align: center;
}

.l-header_item {
    display: inline-block;
    &:not(:first-of-type) {
        margin-left: 1em;
    }
}

.l-header_link {
    display: inline-block;
    padding: 5px 0;
    transition: color 0.3s ease 0s;
    @include mq(lg) {
        &:hover {
            color: $color-accent;
        }
    }
}
//ハンバーガー

.l-header_hamburger {
    padding-bottom: 196px;
    background-color: $color-dark;
    width: 100%;
    height: 100%;
    transform: translateX(105%);
    transition: transform 0.3s;
    position: fixed;
    top: 60px;
    right: 0;
    overflow-y: scroll;
    z-index: 90;
    text-align: center;
    @include rem(16);

    &.active {
        transform: translateX(0);
    }
}

.l-header_hamburger_inner {
    max-width: 300px;
    width: 100%;
    margin: auto;
}

.l-header_hamburger_btn {
    background-color: transparent;
    width: 50px;
    padding: 11px;
    border: none;
    position: fixed;
    top: 10px;
    right: 9px;
    transition: transform 0.3s;
    z-index: 999;

    &.active {
        .l-header_hamburger_btn_bar1 {
            transform: translateY(7px) rotate(-45deg);
        }

        .l-header_hamburger_btn_bar2 {
            opacity: 0;
        }

        .l-header_hamburger_btn_bar3 {
            transform: translateY(-7px) rotate(45deg);
        }
    }
}
.l-header_hamburger_btn_bar1,
.l-header_hamburger_btn_bar2,
.l-header_hamburger_btn_bar3 {
    background-color: $color-black;
    display: block;
    height: 2px;
    border-radius: 3px;
}

.l-header_hamburger_btn_bar1 {
    width: 100%;
}
.l-header_hamburger_btn_bar2 {
    width: 100%;
    margin: 5px 0 0 auto;
}

.l-header_hamburger_btn_bar3 {
    margin: 5px 0 0 auto;
}

.l-header_hamburger_list {
    color: $color-white;
    margin-top: 60px;
}

.l-header_hamburger_item:not(:first-of-type) {
    margin-top: 40px;
}

.l-header_hamburger_link {
    padding: 15px;
}

.l-header_btn {
    margin-top: 40px;
    @include mq(xl) {
        @include rem(14);
        margin-top: 0;
    }
}

.fixed {
    overflow: hidden;
}
