@use "../foundation" as *;

.l-footer {
    text-align: center;
    padding-top: 40px;
    padding-bottom: 20px;
    background-color: $color-base;
}

.l-footer_logo {
    margin: 0 auto;
}

.l-footer_copyright {
    margin-top: 20px;
    @include rem(12);
}

/*====================================================================================================
# top
====================================================================================================*/


.l-footer_top {
    position: fixed;
    right: 70px;
    bottom: 64px;
    z-index: 1;

    @include mq(md){
        right: 80px;
        bottom: 64px;
    }
}

.l-footer_link{
    position: relative;
    &::before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 50%;
        background: $color-dark;
        width: 48px;
        height: 48px;
    }
    &::after{
        content: '';
        position: absolute;
        top: 20px;
        left: 18px;
        transform: rotate(45deg);
        border-top: 3px solid $color-white;
        border-left: 3px solid $color-white;
        width: 10px;
        height: 10px;
    }
    @include mq(lg){
        &:hover::before{
            background: $color-main;
        }
    }
}
