@use "./mixin" as *;
@use "./variable" as *;

html {
    font-size: 62.5%;
}

Ï body {
    font-family: $font-family;
    background-color: $color-white;
    color: $color-black;
    line-height: 1.7;
}

img {
    width: 100%;
    height: auto;
    vertical-align: bottom;
}

*:focus {
    outline: none;
}

a {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    cursor: pointer;
}
//PCのみ表示

.is-pc {
    display: none;
    @include mq(lg) {
        display: block;
    }
}

//スマホのみ表示
.is-sp {
    @include mq(lg) {
        display: none;
    }
}
