@use "../foundation" as *;

.l-main {
    margin-top: 60px;
    @include mq(lg) {
        margin-top: 100px;
    }
}

/*====================================================================================================
# news
====================================================================================================*/

.l-main_news_list {
    margin: 40px auto 0;
    width: 80%;
    @include mq(lg) {
        margin-top: 80px;
    }
}

.l-main_news_item {
    &:not(:first-of-type) {
        margin-top: 40px;
        @include mq(lg) {
            margin-top: 32px;
        }
    }
}

.l-main_news_date {
    @include rem(16);
    line-height: 2;
}

.l-main_news_tag {
    @include rem(12);
    display: inline-block;
    background-color: $color-accent;
    color: $color-white;
    text-transform: uppercase;
    margin-left: 16px;
    padding: 2px 6px;
}

.l-main_news_title {
    @include rem(18);
    margin-top: 8px;
    font-weight: bold;
}

.l-main_news_text {
    @include rem(16);
    margin-top: 8px;
    @include mq(lg) {
        margin-left: 2em;
    }
}

/*====================================================================================================
# about
====================================================================================================*/

.l-main_about_content {
    margin: 40px auto 0;
    @include mq(lg) {
        width: 90%;
        display: flex;
        margin-top: 80px;
    }
}

.l-main_about_item {
    flex: 0 1 60%;
    background: $color-white;
    padding: 30px;
    @include mq(lg) {
        padding: 60px;
    }
}

.l-main_about_text {
    @include rem(16);
    line-height: 2;
}

.l-main_about_img {
    margin-top: 30px;
    @include mq(lg) {
        flex: 0 1 40%;
        margin-left: -3%;
        margin-top: 100px;

        img {
            height: 300px;
            object-fit: cover;
        }
    }
}

/*====================================================================================================
# service
====================================================================================================*/
.l-main_service_list {
    margin-top: 40px;

    @include mq(lg) {
        display: flex;
        flex-wrap: wrap;
        margin-top: 80px;
    }
}
.l-main_service_item {
    background-color: $color-base;
    border-radius: 4px;
    padding: 20px;
    width: 100%;
    &:not(:first-of-type) {
        margin-top: 30px;
    }

    @include mq(lg) {
        width: calc(33% - 30px * 2 / 3);

        &:not(:first-of-type) {
            margin-top: 0;
        }
        &:not(:nth-child(3n)) {
            margin-right: 30px;
        }
        &:nth-of-type(n + 4) {
            margin-top: 50px;
        }
    }
}

.l-main_service_thumbnail {
    position: relative;
    overflow: hidden;
    &:before {
        content: "";
        display: block;
        padding-top: 66.66%;
    }

    img {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
    }
}

.l-main_service_body {
    margin-top: 2em;
}

.l-main_service_title {
    @include rem(20);
    font-weight: bold;
}

.l-main_service_text {
    @include rem(16);
    margin-top: 0.5em;
    line-height: 1.8;
}

/*====================================================================================================
# price
====================================================================================================*/

.l-main_price_content {
    margin-top: 40px;
    @include rem(16);
    @include mq(lg) {
        margin-top: 80px;
        margin-left: 5rem;
    }
}

.l-main_price_text span {
    font-weight: bold;
}

.l-main_price_table_title {
    margin-top: 60px;
    font-weight: bold;
    display: inline-block;
    padding: 1rem;
    @include rem(20);
    color: $color-black;
    &:before {
        content: "●";
        color: $color-dark;
        margin-right: 8px;
    }

    @include mq(lg) {
        @include rem(26);
        margin-top: 80px;
        margin-left: 2em;
        padding: 1rem 4rem;
    }
}

.l-main_price_table {
    margin: 40px auto 0;
    width: 100%;
    border-collapse: collapse;
    @include mq(lg) {
        width: 60%;
    }
}

.l-main_price th,
td {
    text-align: center;
    padding: 1em;
    width: 50%;
}

.l-main_price_table th {
    font-weight: bold;
    border-bottom: solid 5px $color-accent;
    color: $color-accent;
    @include rem(18);
    @include mq(lg) {
        @include rem(20);
    }
}

.l-main_price_table td {
    @include rem(16);
    border-bottom: 1px solid $color-accent;
    @include mq(lg) {
        @include rem(18);
    }
}

/*====================================================================================================
# flow
====================================================================================================*/
.l-main_flow_content {
    margin-top: 40px;
    @include mq(lg) {
        display: flex;
        margin-top: 80px;
    }
}

.l-main_flow_img {
    @include mq(lg) {
        height: auto;
        width: 30%;
        margin-top: 120px;
    }
    img {
        height: 300px;
        object-fit: cover;
        @include mq(lg) {
            height: auto;
        }
    }
}

.l-main_flow_list {
    margin-top: 30px;
    @include mq(lg) {
        width: 65%;
        margin-left: 10%;
    }
}

.l-main_flow_item {
    &:not(:first-of-type) {
        margin-top: 30px;
    }
}

.l-main_flow_list_title {
    @include rem(20);
    color: $color-accent;
    font-weight: bold;

    @include mq(lg) {
        @include rem(28);
    }
}

.l-main_flow_number {
    @include rem(40);
    font-weight: bold;
    display: inline-block;
    margin-right: 0.5em;
    color: $color-accent;
    @include mq(lg) {
        @include rem(60);
    }
}

.l-main_flow_list_text {
    @include rem(18);
    margin-top: 10px;
    line-height: 1.8;
    @include mq(lg) {
        margin-left: 121px;
    }
}

.l-main_flow_list_text span {
    font-weight: bold;
}

.l-main_flow_btn {
    color: $color-white;
    width: 256px;
    text-align: center;
    background-color: $color-dark;
    @include rem(16);
    font-weight: bold;
    margin: 40px auto 0;
    border-radius: 30px;

    @include mq(lg) {
        transition: background-color 0.3s ease 0s;
        &:hover {
            background-color: $color-main;
        }
    }
}

.l-main_flow_link {
    display: inline-block;
    padding: 1em 3em;
}

/*====================================================================================================
# access
====================================================================================================*/
.l-main_access_content {
    margin-top: 40px;
    @include mq(lg){
        width: 80%;
        margin: 80px auto 0;
        display: flex;
    }
}

.l-main_access_map {
    @include mq(lg){
        width: 55%;
    }
}

.iframe-wrap {
    position: relative;
    width: 100%;
    padding-top: 66.66%;
}

.iframe-wrap iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.l-main_access_list {
    @include rem(18);
    margin: 30px auto 0;
    @include mq(lg){
        width: 40%;
        margin-left: 5%;
        margin-top: 0;
    }
}

.l-main_access_text {
    color: $color-dark;
    &:not(:first-of-type) {
        margin-top: 20px;
    }
}

.l-main_access_text span {
    font-weight: bold;
    display: inline-block;
    margin-right: 2em;
    width: 72px;
}

/*====================================================================================================
# contact
====================================================================================================*/
.l-main_contact_content {
    width: 100%;
    @include rem(18);
    margin: 60px auto 0;
    line-height: 1.8;
    @include mq(lg){       
        width: 65%;
    }
}

form {
    width: 100%;
    max-width: 800px;
    margin: 60px auto 0;
    @include rem(18);
    @include mq(lg) {
        margin-top: 80px;
    }
}

form-inner {
    width: 90%;
    margin: 0 auto;
    @include mq(md) {
        width: 70%;
    }
}

.l-main_contact_attention {
    margin-top: 8px;
    @include rem(16);
}

.l-main_contact_attention--color {
    color: red;
}

.l-main_contact_list {
    margin-top: 30px;
}

.l-main_contact_item {
    margin-top: 20px;
}

dd {
    margin-top: 18px;
}

input,
textarea {
    width: 100%;
}

input {
    appearance: none;
    background-color: $color-white;
    background-image: none;
    border: 2px solid rgba(0, 0, 0, 0.2);
    border-radius: 0;
    color: inherit;
    font-family: inherit;
    @include rem(18);
    padding: .5em 0.8em;
    width: 100%;
}

input:focus {
    border: 2px solid rgba(0, 0, 0, 0.5);
    box-shadow: none;
    outline: none;
}

textarea {
    appearance: none;
    background-color: $color-white;
    background-image: none;
    border: 2px solid rgba(0, 0, 0, 0.2);
    border-radius: 0;
    color: inherit;
    font-family: inherit;
    font-size: 1em;
    height: 300px;
    padding: 0.4em 0.8em;
    width: 100%;
    resize: vertical;
}

textarea:focus {
    border: 2px solid rgba(0, 0, 0, 0.5);
    box-shadow: none;
    outline: none;
}

.l-main_contact_submit {
    width: 256px;
    margin: 60px auto 0;
}

input[type="submit"] {
    background-color: $color-dark;
    background-image: none;
    border:none;
    border-radius: 0;
    color: $color-white;
    font-weight: bold;
    cursor: pointer;
    display: inline-block;
    @include rem(18);
    margin: 0 0 1em;
    padding: 1em;
    text-decoration: none;
    transition: background-color 0.3s ease 0s;
}

input[type="submit"]:hover,
input[type="submit"]:focus {
    outline: none;
    background-color: $color-main;
    color: $color-white;
}
