@use "../foundation" as *;

.l-fv {
    background: url(../../img/fv.jpg) no-repeat center center / cover;
    height: 100vh;
    position: relative;
}

.l-fv_content {
    color: $color-base;
    width: 100%;
    position: absolute;
    top: 24%;
    // top: 200px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
}

.l-fv_title {
    letter-spacing: 0.2em;
    @include rem(20);
    font-weight: bold;
    font-family: "Yu Mincho Light", "YuMincho", "Yu Mincho", "游明朝体", "ヒラギノ明朝 ProN", "Hiragino Mincho ProN", sans-serif;

    @include mq(md) {
        @include rem(30);
    }
}
